


* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


body{
  font-family: 'Righteous', cursive;
}


.App{
  background: url('./assets/cold-bg.jpg');
  background-size: cover;
  background-position: bottom; 
  transition: 0.4 ease-out;
}

.App.warm {
  background-image: url('./assets/warm-bg.jpg');
}

main{
  min-height: 100vh;
  background-image: linear-gradient(to bottom , rgba(0,0,0,0.2) , rgba(0,0,0,0.75));
  padding: 25px;
}

.brand {
  text-align: center;
  font-size: x-large;
  color: #313131;
  text-shadow: 3px 3px rgba(255, 255, 255, 0.5);

}

.search-box{
  width: 100%;
  margin: 0 0 75px;
  color: brown;
}



.search-box .search-bar{
  display: block;
  width: 100%;
  padding: 15px;
  appearance: none;
  background: none;
  border: none;
  outline: none;

  background-color: rgba( 255, 255, 255, 0.5) ;
  border-radius: 16px 16px 16px 16px;
  margin-top: 35px;
  box-shadow: 0px 5px rgba(0,0,0,0.2) ;

  color: #313131;
  font-size: 20px;

  transition: 0.2s ease-in;
}

.search-box .search-bar :focus{
  background-color: rgba( 255, 255, 0, 0.1);
}

.location-box .location {
  color: #fff;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba( 50, 50, 70, 0.5);
}

.location-box .date{
  color: #fff;
  font-size: 20px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rgba( 50, 50, 70, 0.5);
}

.weather-box{
  text-align: center;
}

.weather-box .temp{
  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba( 255, 255, 255, 0.2);
  border-radius: 16px;
  padding: 15px 25px;
  color: #fff;
  font-size: 102px;
  font-weight: 900;
  text-shadow: 7px 6px rgba( 50, 50, 70, 0.5);
  text-align: center;
  box-shadow: 3px 6px rgba(0,0,0,0.2);
}

.weather-box .weather {
  color: #fff;
  font-size: 48px;
  font-weight: 700;
  text-shadow: 3px 3px rgba( 50, 50, 70, 0.5);
}


.footer {
  position: -webkit-sticky;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: transparent;
  color: white;
  text-align: center;
}
